body{
    font-family: 'Source Sans Pro', sans-serif;
    max-width: 1500px;
    margin: 0 auto;
    padding: 0;
}

.row{
    margin: 0;
    padding: 0;
}

section{
    height: 100vh;
}

@media (max-width: 768px) {
    section{
        height: auto;
    }
}

#mainNav {
    padding-top: 1rem;
    padding-bottom: 1rem;
    background-color: #212529;
}

#mainNav .navbar-toggler {
    padding: 0.75rem;
    font-size: 0.75rem;
    text-transform: uppercase;
    font-weight: 700;
}

#mainNav .navbar-nav:first-of-type{
    border-bottom: 1px solid white;
}

#mainNav .navbar-nav .nav-item .nav-link {
    font-size: 0.95rem;
    color: #fff;
    opacity: 70%;
    letter-spacing: 0.0625em;
    transition: 0.2s ease-in-out;
}

#mainNav .navbar-nav .nav-item .nav-link.active, #mainNav .navbar-nav .nav-item .nav-link:hover {
    opacity: 100%;
} 


@media (min-width: 992px) {
    #mainNav {
        padding-top: 1.5rem;
        padding-bottom: 1.5rem;
        border: none;
        background-color: transparent;
        transition: padding-top 0.3s ease-in-out, padding-bottom 0.3s ease-in-out;
    }
    #mainNav .navbar-nav .nav-item {
        margin-right: 1rem;
    }
    #mainNav .navbar-nav .nav-item:last-child {
        margin-right: 0;
    }
}

header.masthead {
padding: 7rem 7rem 6rem;
text-align: center;
color: #fff;
background-image: url(/static/media/Home.27ff6434.png);
background-repeat: no-repeat;
background-attachment: scroll;
background-position: center center;
background-size: cover;
height: 100vh;
}
header.masthead .masthead-subheading {
font-size: 0.6rem;
line-height: 1rem;
margin-bottom: 5px;
}
header.masthead .masthead-heading {
font-size: 3.25rem;
font-weight: 700;
line-height: 3.25rem;
letter-spacing: 0.1em;
margin-bottom: 2rem;
}
header img{
    width: 40%;
}
header div.container{
    text-align: left;
}

@media (min-width: 768px) {
    header.masthead {
        padding: 8rem 15rem 12.5rem;
    }
    header.masthead .masthead-subheading {
        font-size: 0.8rem;
        line-height: 1rem;
        font-weight: bold;
        margin-bottom: 0.1rem;
    }
    header.masthead .masthead-heading {
        font-size: 3rem;
        font-weight: 400;
        line-height: 3rem;
        letter-spacing: 0.05em;
        margin-bottom: 4rem;
    }
}

header img.arrowDown{
    width: 2rem;
    cursor: pointer;
    transition: 0.5s ease-in-out;
}

header img.arrowDown:hover{
    opacity: .5;
}

.flagContainer{
    margin-top: 0.6rem;
}

.flagContainer p{
    margin-top: 0.1rem;
}

.flagDiv{
    color: white;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    opacity: 70%;
    -webkit-filter: saturate(0);
            filter: saturate(0);
}

.flag{
    width: 20px;
    height: 20px;
}

.flagDiv:hover, .flag2{
    text-decoration: none;
    color: white;
    opacity: 100%;
    -webkit-filter: saturate(1);
            filter: saturate(1);
}

.paddingXaxis{
    padding: 17rem 5rem 12.5rem 15rem;
}

.btn-contact{
    border:1px solid white;
    transition: 0.2s ease-in-out;
}

.btn-contact:hover{
    color: white;
    background-color: rgba(255, 255, 255, 0.404) !important;
    border:1px solid white;
}

.divisionLine{
    width: 50px;
    height: 3px;
    background: #0C2029;
}

.tittleHighlighted{
    background-color: white;
    color: #0C2029;
    font-weight: 700;
    height: 48px;
    letter-spacing: 0.15em;
    display: inline-block;
}

header div.container{
    border-bottom: 1px solid white;
}

.sectionPadding{
    padding: 5rem 15rem 1rem;
}

.sectionPadding1{
    padding: 2rem 15rem 0;
}

.sectionPadding2{
    padding: 5rem 15rem 0;
}

.sectionTittle{
    font-family: 'Fjalla One', sans-serif !important;
    font-size: 3rem;
    letter-spacing: 0.03em;
}

.sectionDescription{
    font-style: normal;
    font-weight: bold;
    font-size: 0.87rem;
    line-height: 1.1rem;
    letter-spacing: 0.1em;
}

.txt-black{
    color: #313131;
    opacity: 0.56;
}

.btn-blue-cta{
    background: #528DE5;
    border-radius: 30px;
    color: white;
    font-weight: bold;
    font-size: 0.8rem;
    line-height: 1.1rem;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    padding: .5rem 1.2rem;
    transition: all 0.6s ease-in-out;
}

.btn-blue-cta:hover{
    background: white;
    box-shadow: 0px 8px 23px -1px #999999;
}

.btn-transparent{
    background: transparent;
    border: 1px solid white;
    border-radius: 30px;
    color: white;
    font-weight: bold;
    font-size: 0.8rem;
    line-height: 1.1rem;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    padding: .5rem 1.2rem;
    transition: all 0.6s ease-in-out;
}

.btn-transparent:hover{
    background: white;
    box-shadow: 0px 8px 23px -1px #999999;
}

/* INFO */

.carousel-control-prev{
    width: 30%;
}

.carousel-control-next{
    width: 30%;
}

.carousel-indicators li {
    width: 10px;
    height: 10px;
    border-radius: 100%;
    background-color: #C4C4C4;
}

.infoCarousel{
    font-family: 'Fjalla One', sans-serif;
    font-size: 3rem;
    text-align: center;
    line-height: 3rem;
    letter-spacing: 0.08em;
}

